<template>
  <v-data-table
    :headers="headers"
    :items="desserts"
    sort-by="calories"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar flat color="white">
        <v-toolbar-title>{{ $t('managment') }}</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <div class="flex-grow-1"></div>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <div v-for="(permission, index) in user.role.permissions" :key="index">
              <v-btn v-if="permission.name == 'CAN_CREATE' || permission.name == 'CAN_ALL'"  color="primary" dark class="mb-2" v-on="on">{{ $t('newitem') }}</v-btn>
            </div>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.name" v-bind:label="$t('name')" :rules="nameRules" required></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.email" v-bind:label="$t('email')" :rules="emailRules" required></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.password" v-bind:label="$t('password')" :type="'password'" required></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <select  class="form-control" label="role" v-model="editedItem.role_id" :required="true">
                      <option v-bind:value="0">{{$t('clicktochooserole')}}</option>
                      <option v-for="option in options" v-bind:value="option.id" >{{ option.name }}</option>
                    </select>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <div class="flex-grow-1"></div>
              <v-btn color="blue darken-1" text @click="close">{{$t('cancel')}}</v-btn>
              <v-btn color="blue darken-1" text @click="save">{{$t('save')}}</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.action="{ item }">
      <div v-for="(permission, index) in user.role.permissions" :key="index">
        <v-icon
          small
          class="mr-2"
          v-if="permission.name == 'CAN_UPDATE'  || permission.name == 'CAN_ALL'"
          @click="editItem(item)"
        >
          edit
        </v-icon>
        <v-icon
          small
          v-if="permission.name == 'CAN_DELETE' || permission.name == 'CAN_ALL'"
          @click="deleteItem(item)"
        >
          delete
        </v-icon>
      </div>
    </template>
  </v-data-table>
</template>

<script>
  import i18n from '@/plugins/i18n';

  export default {
    data: () => ({
      user: JSON.parse(localStorage.getItem('user')),
      dialog: false,
      nameRules: [
        v => !!v || 'Name is required',
        v => v.length <= 30 || 'Name must be less than 10 characters',
      ],
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid',
      ],
      options:[],
      key:"",
      desserts: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        email: '',
        password: '',
        role_id: 0,
        role: ''
      },
      defaultItem: {
        name: '',
        email: '',
        password: '',
        role_id: '',
        role: {
          id:'',
          name:''
        }
      },
    }),
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
      headers () {
        return [
          {
            text: i18n.t('name'),
            align: 'left',
            sortable: false,
            value: 'name',
            locale: i18n.locale
          },
          { text: i18n.t('email'), value: 'email' },
          { text: i18n.t('roles'), value: 'role.name' },
          { text: i18n.t('actions'), value: 'action', sortable: false },
        ]
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
    },
    created () {
      this.initialize()
    },
    methods: {
      onChange(event) {
            this.editedItem.role_id=event.target.value;
      },
      initialize () {
        // actions in header
        var flag = true;
        for(var i=0; i <  this.user.role.permissions.length; i++) {
          if (this.user.role.permissions[i].name.includes("CAN_ALL") || this.user.role.permissions[i].name.includes('CAN_UPDATE') || this.user.role.permissions[i].name.includes('CAN_DELETE')) {
             flag = false;
          }          
        }
        if (flag === true) {
            this.headers.splice(3, 1);
        }

        // fill data
        this.$api.get(`${process.env.VUE_APP_API_ENDPOINT}admin/role`, {
          headers: {Authorization: 'Bearer ' + localStorage.getItem('token')}
        })
        .then(response => {
          this.options = response.data.data;
          console.log(this.options);
          this.loading = false;
        })
        .catch(error => {
          console.log(error);
          this.$toasted.global.my_app_error();
        });


        this.$api.get(`${process.env.VUE_APP_API_ENDPOINT}admin/user`, {
          headers: {Authorization: 'Bearer ' + localStorage.getItem('token')}
        })
        .then(response => {
          this.desserts = response.data.data;
          this.loading = false;
        })
        .catch(error => {
          console.log(error);
          this.$toasted.global.my_app_error();
        });
      },
      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
        // console.log('df')
      },
      deleteItem (item) {
        const index = this.desserts.indexOf(item)
        // console.log(item);
        const answer = confirm('Are you sure you want to delete this item?')
        if (answer) {
          this.$api.delete(`${process.env.VUE_APP_API_ENDPOINT}admin/user/${item.id}`,
          {
            headers: {Authorization: 'Bearer ' + localStorage.getItem('token')}
          })
          .then(response => {
            // console.log(response);
            // this.desserts = response.data.data;
            this.loading = false;
            this.desserts.splice(index, 1)
          })
          .catch(error => {
            // console.log(error);
            this.$toasted.global.my_app_error({
                message : error.response.data.message
            });
          });
        }
        
      },
      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },
      save () {
        if (this.editedIndex > -1) {
          this.$api.patch(`${process.env.VUE_APP_API_ENDPOINT}admin/user/${this.editedItem.id}`,
          {
            name: this.editedItem.name,
            email: this.editedItem.email,
            password: this.editedItem.password,
            role_id: this.editedItem.role_id
          },
          {
            headers: {Authorization: 'Bearer ' + localStorage.getItem('token')}
          })
          .then(response => {
            this.editedItem = response.data.data;
            this.loading = false;
            this.close();
            Object.assign(this.desserts[this.editedIndex], this.editedItem);
          })
          .catch(error => {
            console.log(error);
            this.$toasted.global.my_app_error({
                message : error.response.data.message
            });
          });
        } else {
          console.log('save ');

          console.log(this.editedItem.role_id);
          this.$api.post(`${process.env.VUE_APP_API_ENDPOINT}admin/user`,
          {
            name: this.editedItem.name,
            email: this.editedItem.email,
            password: this.editedItem.password,
            role_id: this.editedItem.role_id
          },
          {
            headers: {Authorization: 'Bearer ' + localStorage.getItem('token')}
          })
          .then(response => {
            this.editedItem = response.data.data;
            this.loading = false;
            this.desserts.push(this.editedItem);
          })
          .catch(error => {
            // console.log(error);
            this.$toasted.global.my_app_error({
                message : error.response.data.message
            });
          });
        }
        this.close()
      },
    },
  }
</script>